import React from 'react';
import Button from '@mui/material/Button';
import Box from '@mui/material/Box';
import { Typography, Link } from '@mui/material';

import IconButton from '@mui/material/IconButton';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import GitHubIcon from '@mui/icons-material/GitHub';

export default function About() {
    return (
        <Box sx={{ minHeight: '80vh', display: 'flex', alignItems: 'center', justifyContent: 'center', flexDirection: 'column' }}>
            <Typography variant='h1'>I'm Zaichone</Typography>
            <Typography variant='h2'>I'm a frontend engineer</Typography>
            <Typography variant='h3'>I love React and Next.js</Typography>
            <Box>
            <Link href="https://www.linkedin.com/in/zaichone/">
                <IconButton aria-label="LinkedIn">
                    <LinkedInIcon />
                </IconButton>
            </Link>
            <Link href="https://github.com/zaichone">
                <IconButton aria-label="Github">
                    <GitHubIcon />
                </IconButton>
            </Link>
            </Box>
            
        </Box>
    )
}
